import React, { Component } from 'react'
import PropTypes from 'prop-types'

import DiscLoader from '../discLoader'

import './atlasmuseumPageLoader.css'

/**
 * Chargement d'une page sur atlasmuseum
 */
export class AtlasmuseumPageLoader extends Component {
  state = {
    content: '',
    isLoaded: false,
  }

  componentDidMount() {
    const requestInit = {
      method: 'GET',
      mode: 'cors',
      cache: 'default'
    }

    const requestParameters = {
      action: 'parse',
      disabletoc: true,
      prop: 'text',
      redirects: true,
      disableeditsection: true,
      page: 'Actualités',
      format: 'json'
    }

    const requestUrl = 'http://publicartmuseum.net/w/api.php?' + 
      Object.keys(requestParameters).map(function(key) {
        return encodeURIComponent(key) + '=' + encodeURIComponent(requestParameters[key])
      }).join('&')

    fetch(requestUrl, requestInit)
      .then(response => {
        return response.json()
      })
      .then(data => {
        if (!data.error) {
          this.setState({
            isLoaded: true,
            content : data.parse.text['*'].replace('<a href="/wiki', '<a href="http://publicartmuseum.net/wiki'),
          })
        } else {
          this.setState({
            isLoaded: true,
            content : '',
          })
        }
      })
      .catch(error => {
        this.setState({
          isLoaded: true,
          content : '',
        })
      })
  }

  render() {
    const {
      content,
      isLoaded,
    } = this.state

    return (
      <div className="atlasmuseumPageLoader">
        { isLoaded
        ? (
            <div dangerouslySetInnerHTML={{ __html:  content}}/>
          )
        : (
            <DiscLoader />
          )
        }
      </div>
    )
  }
}

AtlasmuseumPageLoader.propTypes = {
  page: PropTypes.string,
}

AtlasmuseumPageLoader.defaultProps = {
  page: null,
}

export default AtlasmuseumPageLoader
