import { takeEvery, call, put } from 'redux-saga/effects'
import { 
  DATA_SEARCH,
  DATA_READY,
  API_ERROR,
} from '../constants/action-types'

export default function* watcherSaga() {
  yield takeEvery(DATA_SEARCH, searchSaga)
}

function* searchSaga(args) {
  try {
    const payload = yield call(getSearch, args)
    yield put({ type: DATA_READY, payload })
  } catch (e) {
    yield put({ type: API_ERROR, payload: e })
  }
}

function getSearch(args) {
  if (args && args.payload && args.payload.searchValue)
    return fetch('http://api.tvmaze.com/search/shows?q=' + args.payload.searchValue, {mode: 'cors'}).then(response =>
      response.json()
    )

  return []
}
